
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
data(){
return{
rm:[],
is_r:true,
title:'每周热门排行',
}
},
methods:{
getlist(){
this.$axios.post("/api/Lenders/get_start_lenders")
.then((results) => {
if(results.code==200 && results.data.list.length>0){
this.rm = results.data.list
}else{
Toast(results.msg)
}
});
}

},
created(){
this.getlist();
},
components:{
XHeader
}
}




